import React, { FunctionComponent, Component } from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import headerNav from '../../content/header-nav.pl.json';
import headerContact from '../../content/header-contact.pl.json';
import messages from '../../content/messages.pl.json';

import SEO from '../components/Seo';
import Layout from '../components/layout/Layout';

const IndexPage: FunctionComponent = (location: any) => {
  const data = useStaticQuery(graphql`
    query IndexMarkdown {
      site {
        siteMetadata {
          title
          defaultLanguage
          availableLanguages
        }
      }

      content: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//home/pl//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            id
            fileAbsolutePath
            frontmatter {
              slug
              title
              source
              position
              file {
                publicURL
              }
            }
            excerpt(format: HTML)
            html
          }
        }
      }

      videos: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//videos//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            id
            fileAbsolutePath
            frontmatter {
              title
              cover {
                publicURL
              }
              videoId
            }
          }
        }
      }

      audios: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//audios//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            id
            fileAbsolutePath
            frontmatter {
              title
              file {
                publicURL
              }
            }
          }
        }
      }

      galleryPhotos: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//gallery//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            id
            fileAbsolutePath
            frontmatter {
              title
              file {
                publicURL
              }
            }
          }
        }
      }

      galleryThumbnails: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "//gallery//" } }
        sort: { fields: [frontmatter___order], order: ASC }
      ) {
        edges {
          node {
            frontmatter {
              title
              file {
                childImageSharp {
                  fixed(width: 400) {
                    ...GatsbyImageSharpFixed
                  }
                }
              }
            }
          }
        }
      }
    }
  `);

  const currentLanguage = 'pl';

  return (
    <>
      <SEO
        lang={currentLanguage}
        title="Krzysztof Grzybowski - Klarnecista"
        description="Oficjalna strona internetowa klarnecisty Krzysztofa Grzybowskiego"
        meta={[]}
      />
      <Layout
        data={data}
        headerNav={headerNav}
        headerContact={headerContact}
        messages={messages}
        location={location}
        currentLanguage={currentLanguage}
      />
    </>
  );
};

export default IndexPage;
